import React from 'react'
import range from 'lodash/range'

import Box from './Box'
import Text from './Text'
import Flex from './Flex'
import Option from './Option'
import Circle from './Circle'



const Options = ({ circle, notsection, onChange, value, id, ...props }) => {
  return (
    <Box textAlign="center" {...props}>
      <Flex mt="0.5em" alignItems="center">
        {!notsection && (
          <Box textAlign="center" fontSize="0.875em" px="0.5em">
            <Text>非常</Text>
            <Text>不符合</Text>
          </Box>
        )}
        {range(5).map((num) => (
          <Flex px="0.75em" flexDirection="column" alignItems="center" width={circle && 1 / 5} key={num}>
            <Box>{num + 1}</Box>
            { circle ? (
              <Box width="2.25em">
                <Circle border="1.675px solid" bg="lightGray" />
              </Box>
            ) : (
              <Option
                type="radio"
                name={id}
                value={`${num + 1}`}
                onChange={onChange}
              />
            )}
          </Flex>
        ))}
         {!notsection && (
          <Box textAlign="center" fontSize="0.875em" px="0.5em">
            <Text>完全</Text>
            <Text>符合</Text>
          </Box>
        )}
      </Flex>
    </Box>
  )
}

export default Options
